.nav-wrapper {
  position: sticky;
  top: 0;
  z-index: 30;
  padding: 0.5rem 0;
  background-color: rgb(0, 0, 0, 0.1);
}
.nav-conten {
  max-width: 1300px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem 0;
  margin: 0 auto;
}
.logo {
  width: 12rem;
  height: auto;
}
.logo span {
  color: #7e61e7;
}
.nav-conten ul {
  display: flex;
  align-items: center;
  list-style: none;
  gap: 0.5rem;
}

.nav-conten li {
  margin: 0 1.5rem;
}
.menu-item {
  text-decoration: none;
  font-size: 0.9rem;
  font-weight: 600;
  color: #fff;
  position: relative;
  cursor: pointer;
}

.menu-item::before {
  content: "";
  width: 2rem;
  height: 0.2rem;
  background: linear-gradient(90deg, #a993fe 0%, #7e61e7 100%);
  border-radius: 0.5rem;
  position: absolute;
  bottom: -0.6rem;
  transform: translateX(-1.5rem);
  transition: all 0.3s ease;
  opacity: 0;
}
.menu-item:hover:before {
  width: 100%;
  transform: translateX(0);
  opacity: 1;
}

.btn {
  background: linear-gradient(90deg, #a993fe 0%, #7e61e7 100%);
  font-size: 0.9rem;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.4rem 1.5rem;
  outline: 1.5px solid transparent;
  border: none;
  color: #fff;
  cursor: pointer;
  border-radius: 0.2rem;
  transition: all 0.3s ease;
  
}
.btn:hover {
  color: #7e61e7;
  background: #000;
  outline: 1.5px solid #7e61e7;
}
.menu-btn {
  width: 2.5rem;
  height: 2.5rem;
  font-size: 1.5rem;
  color: #fff;
  display: none;
}


.menu{
  display: none;
  position: absolute;
  top: 9;
  right: 0.5rem;
  flex-direction: column;
  justify-content: space-between;
  width: 2.25rem;
  height: 2rem;
}
.menu span{
  
  width: 100%;
  height: 0.4rem;
  background-color: #fff;
  border-radius: 0.2rem;
}
@media (max-width: 769px) {
  .menu-btn {
    display: block;
  }

  .nav-conten ul {
    display: none;
    
  }
  .menu{
    display: flex;
  }

}
@media (max-width: 1325px) {
  .nav-wrapper {
    padding: 0 2rem;
  }
}
